body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.BackGroundComponent {
    position: fix;
    width: 100%;
    height: 100%;
    z-index:0;
}
.Circle{
    z-index: 0;
}
.ContactInformationPane{
  font-size:2em;
  text-align: right;
}

.ContactInformation{
  float:right;
  color:white;
}
.ContentFrame .title{
    font-size: 2em;
    font-weight: bold;
    text-shadow: 
      0 0 1px #8080ed, 0 0 3px #8080ed, 
      0 0 10px #8080ed;
}

.ContentFrame{
    margin: 1em 0em;
}

.ContentFrame .content{
    border: 1px solid #8080ed;
    box-shadow: 0px 0px 1em #8080ed;

    text-align: left;

    font-size: 1.2em;

    border-radius: .5em;
    padding:.5em;
    display:flex;
    flex-wrap:wrap;
    margin: .5em .5em;

    background: linear-gradient(20deg,#1a0053,#000000);
}
.MyProfile {
  font-size: 3em;
  position: relative;
  width:100%;
  height:8em;
  font-weight: bold;
  text-shadow: 
      0 0 1px #8080ed, 0 0 3px #8080ed, 
      0 0 6px #8080ed, 0 0 10px #8080ed, 
      0 0 30px #8080ed, 0 0 50px #8080ed;
}

.ProfileImg{
  width: 100%;
}

.MyProfile img{
    height:100%;
    margin:0;
    opacity: 0.4;
    object-fit: cover;;
}

.MyProfile .small {
  font-size: 0.7em;
}

.MyProfile .Tamano {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.MyProfile .fadeOut{
  transition: 1s;
  opacity: 0;
  visibility: hidden;
}


.MyProfile .fadeIn{
  transition: 1s;
  opacity: 1;
  visibility: visible;
}


.MyProfile .MyProfile-text {
  position: absolute;
  font-size: .7em;
  width: 100%;
  bottom: 10%;
  left: 50%;
  -webkit-transform: translate(-50%,0%);
          transform: translate(-50%,0%);
}

.conferenceCategory{
    font-size: 1.3em;
}
.SkillComponent{
    font-size: 1.2em;
   
    margin:.5em;
    font-weight: bold;
    border-radius: .5em;
    padding:.5em;
    float: left;
    position:relative;
    box-shadow: 0px 0px .1em #8080ed;

    text-align: center;

    text-shadow: 
      0 0 1px #8080ed, 0 0 3px #8080ed, 
      0 0 6px #8080ed;
}

.SkillComponent img{
    height:1em;
    width:auto;
}





.Skills{
    display:flex;
    flex-wrap:wrap;
}

.Category{
    font-weight: bold;
    font-size: 1.4em;
}
.WorkComponent{
    font-size: 1.4em;
    width: 40%;
    margin: .5em 0em;
    border-radius: .5em;
    padding:.5em;
    width: 100%;
    position:relative;
    border: 1px solid #8080ed;
    box-shadow: 0px 0px .1em #8080ed;
}

.WorkContent{
    display: flex;
}

.WorkTitle{
    font-weight: bold;
    text-shadow: 
    0 0 1px #8080ed, 0 0 3px #8080ed, 
    0 0 6px #8080ed;
}

.WorkComponent img{
    width: 5em;
    height:5em;
    overflow: hidden;
    border-radius: 1em;
}

.WorksDetails{
    padding: 0em .5em;
    width: calc(100% - 5em);
}

.tags{
    /* color: #464692; */
    font-size: .8em;
    /* background-color: rgb(63, 0, 63); */
    border: 1px solid #8080ed;
    box-shadow: 0px 0px .2em #8080ed;
    padding:0.1em 0.7em;
    border-radius: 1em;
    margin: 0.1em 0.3em;
    float:left;
}




.Works{
    display:flex;
    flex-wrap:wrap;
}
.HomePage{
    position:relative;
    max-width:720px;
    text-align: center;
    margin-right:auto;
    margin-left:auto;
    z-index: 10;
}
