.MyProfile {
  font-size: 3em;
  position: relative;
  width:100%;
  height:8em;
  font-weight: bold;
  text-shadow: 
      0 0 1px #8080ed, 0 0 3px #8080ed, 
      0 0 6px #8080ed, 0 0 10px #8080ed, 
      0 0 30px #8080ed, 0 0 50px #8080ed;
}

.ProfileImg{
  width: 100%;
}

.MyProfile img{
    height:100%;
    margin:0;
    opacity: 0.4;
    object-fit: cover;;
}

.MyProfile .small {
  font-size: 0.7em;
}

.MyProfile .Tamano {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.MyProfile .fadeOut{
  transition: 1s;
  opacity: 0;
  visibility: hidden;
}


.MyProfile .fadeIn{
  transition: 1s;
  opacity: 1;
  visibility: visible;
}


.MyProfile .MyProfile-text {
  position: absolute;
  font-size: .7em;
  width: 100%;
  bottom: 10%;
  left: 50%;
  transform: translate(-50%,0%);
}
